<template>
  <div class="generate-qr">
    <h1>Generate QR Code</h1>
    <div class="form-container">
      <div class="form-group">
        <label for="referenceId">Reference ID:</label>
        <input
          type="text"
          id="referenceId"
          v-model.trim="transaction.referenceId"
          required
        />
      </div>
      <div class="form-group">
        <label for="customerName">Customer Name:</label>
        <input
          type="text"
          id="customerName"
          v-model.trim="transaction.customerName"
          required
        />
      </div>
      <div class="form-group">
        <label for="amount">Amount:</label>
        <input
          type="number"
          id="amount"
          v-model.number="transaction.amount"
          min="0.01"
          step="0.01"
          required
        />
      </div>
      <div class="form-group">
        <label for="status">Status:</label>
        <select id="status" v-model="transaction.status" required>
          <option value="" disabled>Select status</option>
          <option value="PENDING">Pending</option>
          <option value="COMPLETED">Completed</option>
          <option value="FAILED">Failed</option>
        </select>
      </div>
      <button @click="generateQR" class="btn-primary">Generate QR</button>
    </div>

    <div v-if="showQR" class="qr-container">
      <h2>Scan this QR Code</h2>
      <div class="qr-code">
        <img :src="qrCodeDataUrl" alt="QR Code" />
      </div>
      <div class="qr-actions">
        <button @click="downloadQR" class="btn-secondary">Download QR</button>
        <button @click="saveTransaction" class="btn-primary">
          Save Transaction
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import QRCode from 'qrcode'
import api from '../api'

export default {
  name: 'GenerateQRView',
  data () {
    return {
      transaction: {
        referenceId: '',
        customerName: '',
        amount: null,
        status: ''
      },
      qrCodeDataUrl: '',
      showQR: false
    }
  },
  methods: {
    async generateQR () {
      // Validasi form input
      if (
        !this.transaction.referenceId.trim() ||
        !this.transaction.customerName.trim() ||
        this.transaction.amount === null ||
        this.transaction.amount <= 0 ||
        !this.transaction.status
      ) {
        alert('Please fill in all required fields with valid data')
        return
      }

      try {
        // Generate QR code dengan data transaksi yang sudah di-JSON string
        const qrData = JSON.stringify(this.transaction)
        this.qrCodeDataUrl = await QRCode.toDataURL(qrData)
        this.showQR = true
      } catch (error) {
        console.error('Error generating QR code:', error)
        alert('Failed to generate QR code')
      }
    },
    downloadQR () {
      if (!this.qrCodeDataUrl) return
      const link = document.createElement('a')
      link.download = `transaction-${this.transaction.referenceId}.png`
      link.href = this.qrCodeDataUrl
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    },
    async saveTransaction () {
      try {
        const response = await api.createTransaction(this.transaction)
        alert(`Transaction saved with ID: ${response.data.id}`)

        // Reset form setelah simpan
        this.transaction = {
          referenceId: '',
          customerName: '',
          amount: null,
          status: ''
        }
        this.qrCodeDataUrl = ''
        this.showQR = false
      } catch (error) {
        console.error('Error saving transaction:', error)
        alert('Failed to save transaction')
      }
    }
  }
}
</script>

<style scoped>
/* Tambahkan styling sesuai kebutuhan */
</style>
