<template>
  <div class="scan-qr">
    <h1>Scan QR Code</h1>

    <div class="scanner-container">
      <div v-if="!scanned">
        <p>Point your camera at a transaction QR code to scan it.</p>
        <qrcode-stream @decode="onDecode" @init="onInit"></qrcode-stream>
      </div>

      <div v-if="scanned" class="result-container">
        <h2>QR Code Scanned Successfully!</h2>
        <div class="transaction-details">
          <h3>Transaction Details:</h3>
          <table>
            <tr>
              <td>Reference ID:</td>
              <td>{{ transaction.referenceId }}</td>
            </tr>
            <tr>
              <td>Customer Name:</td>
              <td>{{ transaction.customerName }}</td>
            </tr>
            <tr>
              <td>Amount:</td>
              <td>{{ transaction.amount }}</td>
            </tr>
            <tr>
              <td>Status:</td>
              <td>{{ transaction.status }}</td>
            </tr>
          </table>
        </div>

        <div class="action-buttons">
          <button @click="processTransaction" class="btn-primary">
            Process Transaction
          </button>
          <button @click="resetScanner" class="btn-secondary">
            Scan Again
          </button>
        </div>
      </div>
    </div>

    <div v-if="error" class="error-message">
      {{ error }}
    </div>
  </div>
</template>

<script>
import { QrcodeStream } from 'vue-qrcode-reader'
import api from '../api'

export default {
  name: 'ScanQRView',
  components: {
    QrcodeStream
  },
  data () {
    return {
      transaction: {
        referenceId: '',
        customerName: '',
        amount: 0,
        status: ''
      },
      scanned: false,
      error: '',
      camera: 'auto'
    }
  },
  methods: {
    async onDecode (result) {
      try {
        const qrData = JSON.parse(result)
        this.transaction = {
          referenceId: qrData.referenceId || '',
          customerName: qrData.customerName || '',
          amount: qrData.amount || 0,
          status: qrData.status || 'PENDING'
        }
        this.scanned = true
        this.error = ''
      } catch (error) {
        this.error = 'Invalid QR code format. Please try again.'
        console.error('Error parsing QR data:', error)
      }
    },
    async processTransaction () {
      this.error = ''
      try {
        // Kirim data transaksi ke backend untuk diproses
        const response = await api.post(
          '/transactions/process',
          this.transaction
        )

        if (response.data.success) {
          alert('Transaction processed successfully!')
          this.resetScanner()
        } else {
          this.error =
            response.data.message || 'Failed to process transaction.'
        }
      } catch (err) {
        this.error = 'Error processing transaction. Please try again.'
        console.error(err)
      }
    },
    resetScanner () {
      this.transaction = {
        referenceId: '',
        customerName: '',
        amount: 0,
        status: ''
      }
      this.scanned = false
      this.error = ''
    },
    onInit (promise) {
      promise.catch((error) => {
        this.error = 'Camera initialization failed.'
        console.error(error)
      })
    }
  }
}
</script>

<style scoped>
/* CSS styling sesuai kebutuhan */
</style>
