<template>
  <div class="transactions-view">
    <h1>Transactions List</h1>

    <div v-if="loading" class="loading">Loading transactions...</div>
    <div v-if="error" class="error-message">{{ error }}</div>

    <table v-if="transactions.length" class="transactions-table">
      <thead>
        <tr>
          <th>Reference ID</th>
          <th>Customer Name</th>
          <th>Amount</th>
          <th>Status</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="tx in transactions" :key="tx.id">
          <td>{{ tx.referenceId }}</td>
          <td>{{ tx.customerName }}</td>
          <td>{{ tx.amount }}</td>
          <td>{{ tx.status }}</td>
          <td>
            <button @click="viewDetails(tx)" class="btn-secondary">View</button>
            <button @click="deleteTransaction(tx.id)" class="btn-danger">
              Delete
            </button>
          </td>
        </tr>
      </tbody>
    </table>

    <div v-if="!transactions.length && !loading">No transactions found.</div>

    <!-- Modal for showing transaction details -->
    <div v-if="showModal" class="modal-overlay" @click.self="closeModal">
      <div class="modal-content">
        <h2>Transaction Details</h2>
        <table>
          <tr>
            <td>Reference ID:</td>
            <td>{{ selectedTransaction.referenceId }}</td>
          </tr>
          <tr>
            <td>Customer Name:</td>
            <td>{{ selectedTransaction.customerName }}</td>
          </tr>
          <tr>
            <td>Amount:</td>
            <td>{{ selectedTransaction.amount }}</td>
          </tr>
          <tr>
            <td>Status:</td>
            <td>{{ selectedTransaction.status }}</td>
          </tr>
        </table>
        <button @click="closeModal" class="btn-primary">Close</button>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../api'

export default {
  name: 'TransactionsView',
  data () {
    return {
      transactions: [],
      loading: false,
      error: '',
      showModal: false,
      selectedTransaction: {}
    }
  },
  methods: {
    async fetchTransactions () {
      this.loading = true
      this.error = ''
      try {
        const response = await api.getTransactions()
        this.transactions = response.data
      } catch (err) {
        console.error('Failed to fetch transactions:', err)
        this.error = 'Failed to load transactions.'
      } finally {
        this.loading = false
      }
    },
    viewDetails (transaction) {
      this.selectedTransaction = transaction
      this.showModal = true
    },
    closeModal () {
      this.showModal = false
      this.selectedTransaction = {}
    },
    async deleteTransaction (id) {
      if (!confirm('Are you sure you want to delete this transaction?')) return

      try {
        await api.deleteTransaction(id)
        this.transactions = this.transactions.filter((tx) => tx.id !== id)
        alert('Transaction deleted successfully.')
      } catch (err) {
        console.error('Failed to delete transaction:', err)
        alert('Failed to delete transaction.')
      }
    }
  },
  mounted () {
    this.fetchTransactions()
  }
}
</script>

<style scoped>
.transactions-view {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
}

.loading,
.error-message {
  margin: 20px 0;
  color: #d9534f;
}

.transactions-table {
  width: 100%;
  border-collapse: collapse;
}

.transactions-table th,
.transactions-table td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: left;
}

.transactions-table th {
  background-color: #f5f5f5;
}

.btn-secondary {
  background-color: #6c757d;
  color: white;
  border: none;
  padding: 6px 12px;
  margin-right: 8px;
  cursor: pointer;
  border-radius: 4px;
}

.btn-danger {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 6px 12px;
  cursor: pointer;
  border-radius: 4px;
}

.btn-secondary:hover {
  background-color: #5a6268;
}

.btn-danger:hover {
  background-color: #c82333;
}

/* Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px 30px;
  border-radius: 6px;
  max-width: 400px;
  width: 90%;
}

.modal-content table {
  width: 100%;
  border-collapse: collapse;
}

.modal-content td {
  padding: 6px 8px;
  border-bottom: 1px solid #eee;
}

.btn-primary {
  background-color: #42b983;
  color: white;
  border: none;
  padding: 8px 16px;
  margin-top: 15px;
  cursor: pointer;
  border-radius: 4px;
}

.btn-primary:hover {
  background-color: #369870;
}
</style>
